html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

body.fijar-body {
  overflow: hidden;
}

body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #4cb8b3;
}

@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.3rem 0.2rem 0.1rem 0.3rem #666;
  }
}
.header h1 {
  color: #ffffff;
  text-align: center;
  margin: 0;
}

.header .contenido-header {
  padding: 2rem;
  gap: 2rem;
}

@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: #ffffff;
  font-size: 2.4rem;
  margin-bottom: 1rem;
  text-align: center;
}

@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #fef46d;
}

.prueba {
  text-transform: uppercase;
  font-size: 2.4rem;
  color: #fef46d;
  letter-spacing: 1px;
}

.video {
  max-height: 70rem;
  min-height: 60rem;
  overflow: hidden;
  position: relative;
}

.video .overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgb(92, 44, 126);
  background: linear-gradient(90deg, rgba(92, 44, 126, 0.2) 7%, rgb(244, 5, 234) 96%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.video video {
  width: auto;
  display: block;
}

@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}
.contenido-video {
  z-index: 10;
}

.contenido-video h2 {
  color: #ffffff;
  font-size: 7rem;
  margin: 0;
}

.contenido-video p {
  display: inline-block;
  background-color: #f53756;
  color: #ffffff;
  padding: 1rem 2rem;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

.sobre-festival {
  padding: 5rem 0;
}

@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  margin-bottom: 2rem;
}

.contenido-festival .fecha {
  color: #f53756;
  font-weight: 700;
}

.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}

.lineup h3,
.lineup p {
  text-align: center;
  color: #ffffff;
}

.lineup .dia {
  text-transform: uppercase;
  margin-bottom: 5rem;
}

.escenarios-contenedor {
  margin-bottom: 3rem;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor p {
  color: #000000;
}

.bg-amarillo {
  background-color: #fef46d;
}

.bg-amarillo li:nth-child(odd) {
  background-color: #fdee21;
}

.bg-verde {
  background-color: #4cb8b3;
}

.bg-verde li:nth-child(odd) {
  background-color: #388e8a;
}

.escenario {
  border-radius: 1rem;
  margin-bottom: 2rem;
  height: 660px;
  width: 600px;
  padding-top: 2rem;
}

.escenario p::after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.edm .nombre-escenario:after {
  background-image: url("../img/icono_edm.png");
}

.rock .nombre-escenario:after {
  background-image: url("../img/icono_rock.png");
}

.calendario {
  margin: 0;
  padding: 0;
  list-style: none;
}

.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}

.calendario li span {
  font-weight: 400;
}

.galeria {
  padding: 5rem 0;
}

.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}

.overlay picture {
  display: block;
  max-width: 90rem;
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #ffffff;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 700;
}

.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
}

.boletos h3 {
  text-align: center;
  color: #ffffff;
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.pase {
  color: #000000;
}

.pase .nombre-pase {
  text-align: center;
  font-size: 2.4rem;
  color: #ffffff;
}

.pase-header, .pase-footer {
  padding: 0.5rem;
}

.pase-header p, .pase-footer p {
  text-align: center;
  color: #ffffff;
}

.pase-header {
  background-color: #f53756;
}

.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}

.pase-body {
  background-color: #ffffff;
}

.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.pase-body ul li, .pase-body ul:nth-child(odd) {
  background-color: #ffffff !important;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}

.pase-footer {
  background-color: #752797;
}

.pase-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  background-color: #f53756;
  padding: 1rem;
}

.site-footer p {
  margin: 0;
  text-align: center;
  color: #ffffff;
}